import React from 'react'
import { Link, useParams } from 'react-router-dom'
import { useGetApllicationQuery } from '../store/api/ApplicationAPI'
import Loader from '../components/Loader'

const CheckStatus = () => {

    const params = useParams()

    const { data, isLoading } = useGetApllicationQuery(params.id)

    if (isLoading) {
        return <Loader />
    }

    return (
        <div className='container'>
            <div className="container">
                <div className="row pt-5 pb-5 justify-content-center">
                    <div className="col-md-6">
                        <h2 className="text-uppercase">Visa Status</h2>
                        <div className="border auth-form border-1 p-3 ">
                            <div>
                                <div className="row font-x-large">
                                    <div className="col-5">Visa Number :</div>
                                    <div className="col-7">{data?.application?.visanumber}</div>
                                </div>
                                <div className="row font-x-large">
                                    <div className="col-5">Nationality :</div>
                                    <div className="col-7 text-uppercase">{data?.application?.passportnation}</div>
                                </div>
                                <div className="row font-x-large">
                                    <div className="col-5">Visa Validity :</div>
                                    <div className="col-7">{data?.application?.visavalidity}</div>
                                </div>
                                {data.application.apllicationstatus == "pending" ? <div className="row font-x-large">
                                    <div className="col-5">Visa Status :</div>
                                    <div className="col-7"><p className="fw-bold d-inline-block text-danger" >Under Process</p></div>
                                </div> : <></>}
                                {data.application.apllicationstatus == "accept" && data.application.isadminprinted == "no" ? <> <div className="row font-x-large">
                                    <div className="col-5">Visa Status :</div>
                                    <div className="col-7"><p className="fw-bold d-inline-block text-danger" >READY TO PRINT</p></div>
                                </div>
                                    <div className="row font-x-large">
                                        <div className="col-5">Fees :</div>
                                        <div className="col-7"><p className="fw-bold d-inline-block text-danger" >Visa ready to be printed, please visit nearest service center to pay fees, also fees can be paid through Metrash2 or ministry eServices in Moi Website</p></div>
                                    </div></> : <></>}
                                {data.application.apllicationstatus == "accept" && data.application.isadminprinted == "yes" ? <> <div className="row font-x-large">
                                    <div className="col-5">Visa Status :</div>
                                    <div className="col-7"><p className="fw-bold d-inline-block text-danger text-uppercase" >Already Printed</p>
                                    <p className=" text-danger text-uppercase" style={{ fontSize: '16px' }} >You can now print</p></div>
                                </div>
                                    <div className="pt-3">
                                        <Link className="btn btn-primary" to={`/print/${data?.application._id}`}>
                                            Download Your Visa
                                        </Link>
                                    </div></> : <></>}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CheckStatus