import './App.css';
import { Routes, Route } from "react-router-dom";
import Home from './pages/Home';
import FirstStage from './pages/FirstStage';
import SecondStage from './pages/SecondStage';
import ThirdStage from './pages/ThirdStage';
import Login from './pages/Login';
import Dashboard from './pages/Dashboard';
import VisaStatus from './pages/VisaStatus';
import { Toaster } from 'react-hot-toast';
import VisaApplication from './pages/VisaApplication';
import PrintVisa from './pages/PrintVisa';
import CheckStatus from './pages/CheckStatus';
import EditApplication from './pages/EditApplication';


function App() {
  return (
    <div className="App">
      <Toaster position="top-center" />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/apply/first" element={<FirstStage />} />
        <Route path="/apply/second" element={<SecondStage />} />
        <Route path="/apply/third" element={<ThirdStage />} />
        <Route path="/login" element={<Login />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/status" element={<VisaStatus />} />
        <Route path="/status/:id" element={<CheckStatus />} />
        <Route path="/application/:id" element={<VisaApplication />} />
        <Route path="/print/:id" element={<PrintVisa />} />
        <Route path="/edit/:id" element={<EditApplication />} />
      </Routes>
    </div>
  );
}

export default App;
