import React, { useEffect, useState } from 'react'
import { useGetApllicationPostMutation } from '../store/api/ApplicationAPI'
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';

const VisaStatus = () => {


    const [status, setStaus] = useState({
        passportno: undefined,
        viasnumber: undefined,
        passportnation: "",
    })

    const navigate = useNavigate();

    const [getApllication, { data, isError, error, isLoading, isSuccess }] = useGetApllicationPostMutation()

    const inputHandler = (e) => {
        setStaus({ ...status, [e.target.name]: e.target.value })
    }

    console.log(isLoading, error);

    useEffect(() => {
        if (isError) {
            toast.error(error?.data?.message);
        }
        if (isSuccess) {
            navigate(`/status/${data?.application._id}`)
        }
    }, [isError, isSuccess]);
    const btnHandler = async (e) => {
        console.log(status);

        e.preventDefault();
        const body = status
        await getApllication(body)
    }


    return (
        <div>
            <div className="container">
                <div className="row pt-5 pb-5 justify-content-center">
                    <div className="col-md-6">
                        <h2 className="text-uppercase">Visa Status</h2>
                        <div className="border auth-form border-1 p-3 ">
                            <form action="">
                                <div className="pt-3">
                                    <label htmlFor="" className="form-label fw-bold">
                                        Passport Number
                                    </label>
                                    <input
                                        className="form-control rounded-0"
                                        type="passportno"
                                        name="passportno"
                                        placeholder="Please Enter Your Passport Number"
                                        onChange={(e) => inputHandler(e)}
                                    />
                                </div>
                                <div className="pt-3">
                                    <label htmlFor="" className="form-label fw-bold">
                                        Nationality
                                    </label>
                                    <input
                                        className="form-control rounded-0"
                                        type="text"
                                        name="passportnation"
                                        placeholder="Please Enter Your Nationality"
                                        onChange={(e) => inputHandler(e)}
                                    />
                                </div>
                                <div className="pt-3">
                                    <label htmlFor="visanumber" className="form-label fw-bold">
                                        Visa Number
                                    </label>
                                    <input
                                        className="form-control rounded-0"
                                        type="text"
                                        name="visanumber"
                                        placeholder="Please Enter Visa Number"
                                    />
                                </div>
                                <div className="pt-3">
                                    <button className="btn btn-primary" onClick={btnHandler}>
                                        Check Status
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default VisaStatus