import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const ApplicationAPI = createApi({
    reducerPath: "ApplicationAPI",
    baseQuery: fetchBaseQuery({ baseUrl: "/api/v1/application" }),
    tagTypes: ["application"],
    endpoints: (builder) => ({
        getApllication: builder.query({
            query: (id) => ({
                url: `/get/${id}`
            }),
            providesTags: ["application"]
        }),
        getApllicationPost: builder.mutation({
            query: (body) => ({
                url: `/get/`,
                method: "POST",
                body
            }),
            providesTags: ["application"]
        }),
        CreateApplication: builder.mutation({
            query: ({ body }) => ({
                url: `/create`,
                method: "POST",
                body
            }),
            invalidatesTags: ["application"]
        }),
        updateApplicationStatus: builder.mutation({
            query: (body) => ({
                url: `/update/status`,
                method: "PUT",
                body
            }),
            invalidatesTags: ["application"]
        }),
        updateApplication: builder.mutation({
            query: ({ id, body }) => ({
                url: `/update/${id}`,
                method: "PUT",
                body
            }),
            invalidatesTags: ["application"]
        }),
        deleteApplication: builder.mutation({
            query: (id) => ({
                url: `/delete/${id}`,
                method: "DELETE",
            }),
            invalidatesTags: ["application"]
        }),
        getAllApllication: builder.query({
            query: () => ({
                url: "/getall"
            }),
            providesTags: ["application"]
        }),
        updateAdminPrint: builder.mutation({
            query: (body) => ({
                url: `/print/`,
                method: "PUT",
                body
            }),
            invalidatesTags: ["application"]
        }),
    })
}
)

export const { useCreateApplicationMutation, useDeleteApplicationMutation, useUpdateAdminPrintMutation, useUpdateApplicationStatusMutation, useGetApllicationQuery, useGetApllicationPostMutation, useUpdateApplicationMutation, useGetAllApllicationQuery } = ApplicationAPI 