import React, { useEffect, useState } from 'react'
import ApplicationHeader from '../components/ApplicationHeader'
import { Link, useNavigate } from "react-router-dom"
import formDataVlaue from '../utils/FormData'
import { useCreateApplicationMutation } from '../store/api/ApplicationAPI'
import toast from "react-hot-toast";
import { useDispatch, useSelector } from 'react-redux'
import { setApplication } from '../store/slice/ApplicationSlice'
import { useGetProfileQuery } from '../store/api/AuthAPI'
import Loader from '../components/Loader'

const ThirdStage = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch()
    const { isLoading } = useGetProfileQuery();
    const { application } = useSelector((state) => (state.application))
    const { isAuthorized } = useSelector((state) => state.user);
    const [CreateApplication, { data, isError, isSuccess, error }] = useCreateApplicationMutation()

    useEffect(() => {
        if (isError) {
            toast.error(error.data.message);
        }
        if (isSuccess) {
            toast.success(data.message);
            dispatch(setApplication({
                application: {
                    firstname: "",
                    lastname: "",
                    dateofbirth: "",
                    placeofbirth: "",
                    passportnation: "",
                    passportno: "",
                    passportexpirydate: "",
                    phoneno: "",
                    gender: "",
                    sponsername: "",
                    purposeofvisa: "",
                    profession: "",
                    email: "",
                    education: "",
                    contractyear: "",
                    probationmonth: "",
                    passporttype: "",
                    visanumber: "",
                    applicationumber: "",
                    dateofissue: "",
                    visavalidity: "",
                    applicationumber: "",
                    visaowner: "",
                    basicsalary: "",
                    houseallowance: "",
                    transportallowance: "",
                    foodallowance: "",
                    otherallowance: "",
                    totalsalary: "",
                    ticketduration: "",
                    visatype: ""
                }
            }))
        }


    }, [isError, isSuccess]);

    const submitHandler = async () => {
        await CreateApplication({ body: application })
    }
    if (isLoading) {
        return <Loader />
    }

    if (!isAuthorized) {
        return navigate("/login");
    }
    return (
        <div>
            <ApplicationHeader FirstStage="active-on" SecondStage="active-on" ThirdStage={"active-on"} />
            <div className="container">
                <div className='row p-2'>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>First Name :</p>
                        <p className='d-inline-block'>{application.firstname}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Last Name :</p>
                        <p className='d-inline-block'>{application.lastname}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Date Of Birth :</p>
                        <p className='d-inline-block'>{application.dateofbirth}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Place Of Birth :</p>
                        <p className='d-inline-block'>{application.placeofbirth}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Passport Nationality :</p>
                        <p className='d-inline-block'>{application.passportnation}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Passport Number :</p>
                        <p className='d-inline-block'>{application.passportno}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Passport Expiry Date :</p>
                        <p className='d-inline-block'>{application.passportexpirydate}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Phone Number :</p>
                        <p className='d-inline-block'>{application.phoneno}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Gender :</p>
                        <p className='d-inline-block'>{application.gender}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Sponser Name :</p>
                        <p className='d-inline-block'>{application.sponsername}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Purpose Of Visa :</p>
                        <p className='d-inline-block'>{application.purposeofvisa}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Profession :</p>
                        <p className='d-inline-block'>{application.profession}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Email :</p>
                        <p className='d-inline-block'>{application.email}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Education :</p>
                        <p className='d-inline-block'>{application.education}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Contract Duration Year :</p>
                        <p className='d-inline-block'>{application.contractyear}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>No Of Probation Months :</p>
                        <p className='d-inline-block'>{application.probationmonth}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Visa Number :</p>
                        <p className='d-inline-block'>{application.visanumber}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Application Number :</p>
                        <p className='d-inline-block'>{application.applicationumber}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Description Of Visa Owner :</p>
                        <p className='d-inline-block'>{application.visaowner}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Passport Type :</p>
                        <p className='d-inline-block'>{application.passporttype}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Date Of Issue :</p>
                        <p className='d-inline-block'>{application.dateofissue}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Visa Validity :</p>
                        <p className='d-inline-block'>{application.visavalidity}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Basic Salary :</p>
                        <p className='d-inline-block'>{application.basicsalary}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>House Allowance :</p>
                        <p className='d-inline-block'>{application.houseallowance}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Transport Allowance :</p>
                        <p className='d-inline-block'>{application.transportallowance}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Food Allowance :</p>
                        <p className='d-inline-block'>{application.foodallowance}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Other Allowance :</p>
                        <p className='d-inline-block'>{application.otherallowance}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Total Salary :</p>
                        <p className='d-inline-block'>{application.totalsalary}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Ticket Duration :</p>
                        <p className='d-inline-block'>{application.ticketduration}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Ticket Duration :</p>
                        <p className='d-inline-block'>{application.ticketduration}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Ticket Duration :</p>
                        <p className='d-inline-block'>{application.ticketduration}</p>
                    </div>
                    <div className="col-md-4 p-3">
                        <p className='d-inline-block pe-4'>Visa Type :</p>
                        <p className='d-inline-block'>{application.visatype}</p>
                    </div>

                </div>
                <div className="row p-2 ">
                    <div className='col-md-6'>
                        <Link className='btn btn-primary' to={"/apply/second"} >Previous</Link>
                    </div>
                    <div className='col-md-6 text-end'>
                        <Link className='btn btn-primary' onClick={submitHandler} >Submit</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ThirdStage