import React from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
    return (
        <div className='home'>
            <div className="row h-100 justify-content-center align-items-center">
                <div className="col-12 text-center">
                    <Link className='btn btn-primary home-btn me-2' to={"/status"}> Visa Status</Link>
                    <Link className='btn btn-primary home-btn' to={"Login"}>Login</Link>
                </div>
            </div>
        </div>
    )
}

export default Home