import React, { useRef } from 'react';
import { useParams } from 'react-router-dom';
import { useGetApllicationQuery } from '../store/api/ApplicationAPI';
import logo from "../assets/0001.jpg"
import Barcode from "react-barcode"
import Loader from '../components/Loader';
import { useReactToPrint } from "react-to-print";

const PrintVisa = () => {

    const params = useParams()

    const { data, isLoading } = useGetApllicationQuery(params.id)

    const componentRef = useRef(null);

    const handlePrint = useReactToPrint({
        contentRef: componentRef
    }
    );


    if (isLoading) {
        return <Loader />
    }

    if (data?.application.apllicationstatus == "pending" || data?.application.apllicationstatus == "reject") {
        return <h1 className='text-center'>Your visa is {data?.application.apllicationstatus}</h1>
    }

    return (
        <div className="container">
            <div className="my-5">
                <div className="row d-flex justify-content-center mb-5">
                    <button className="btn btn-success col-md-5" onClick={handlePrint}>
                        <i className="fa fa-print" /> Download Visa
                    </button>
                </div>
                <div className='border p-5' ref={componentRef}>
                    <header className="visa" id='visa' >
                        <div id="logo">
                            <img src={logo} alt="Logo" width={"100%"} />
                        </div>
                        <Barcode value={data?.application?.visanumber} height={"50px"} displayValue={"false"} />
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Visa Number :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.visanumber}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Application Number :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.applicationumber}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Date Of Issue :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.dateofissue}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Visa Validity :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.visavalidity}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Description Of Visa Owner :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.visaowner}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Visa Type :</p>
                                <p className='d-inline-block font-x-large text-uppercase'>{data?.application?.visatype}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Purpose Of Visa :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.purposeofvisa}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Duration Of Visa :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.contractyear}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Name :</p>
                                <p className='d-inline-block font-x-large'>{`${data?.application?.firstname} ${data?.application?.lastname}`}</p>
                            </div>
                            <div className="col-6">
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Nationality :</p>
                                <p className='d-inline-block font-x-large text-uppercase'>{data?.application?.passportnation}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Gender :</p>
                                <p className='d-inline-block font-x-large text-uppercase'>{data?.application?.gender}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Passport Number / Type :</p>
                                <div className='d-inline-block '><p className='font-x-large'>{data?.application?.passportno}</p>
                                    <p className='font-x-large text-uppercase'>{data?.application?.passporttype}</p></div>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Passport Validity :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.passportexpirydate}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Date Of Birth :</p>
                                <p className='d-inline-block font-x-large'>{data?.application?.dateofbirth}</p>
                            </div>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Profession :</p>
                                <p className='d-inline-block font-x-large text-uppercase'>{data?.application?.profession}</p>
                            </div>
                        </div>
                        <div className='row  pt-1'>
                            <div className="col-6">
                                <p className='d-inline-block pe-4 font-x-large'>Sponser Name :</p>
                                <p className='d-inline-block font-x-large text-uppercase'>{data?.application?.sponsername}</p>
                            </div>
                            <div className="col-6">
                            </div>
                        </div>
                    </header>
                </div>
            </div>
        </div>
    )
}

export default PrintVisa