import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Loader from '../components/Loader'
import { useGetApllicationQuery, useUpdateApplicationMutation } from '../store/api/ApplicationAPI'
import toast from 'react-hot-toast'

const EditApplication = () => {
    const params = useParams()
    const navigate = useNavigate()

    const { data, isLoading, isSuccess } = useGetApllicationQuery(params.id)
    const [updateApplication, { isError, isSuccess: updateApplicationIsSuccess, error, data: updateApplicationData }] = useUpdateApplicationMutation()
    const [application, setApplication] = useState()

    const changeHandler = (e) => {
        setApplication({ ...application, [e.target.name]: e.target.value })
    }

    const btnHandler = async (e) => {
        e.preventDefault()
        await updateApplication({ id: params.id, body: application })
    }

    useEffect(() => {
        if (isSuccess) {
            setApplication(data?.application)
        }
        if (isError) {
            toast.error(error.data.message);
        }
        if (updateApplicationIsSuccess) {
            toast.success(updateApplicationData.message);
            navigate("/dashboard")
        }

    }, [isSuccess, isError, updateApplicationIsSuccess])

    if (isLoading) {
        return <Loader />
    }

    return (
        <div>
            <div className='container'>
                <h3>Edit Application</h3>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="firstname">First Name</label>
                        <input className='form-control' type="text" name="firstname" id="firstname" value={application?.firstname} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="">Last Name</label>
                        <input className='form-control' type="text" name="lastname" id="lastname" value={application?.lastname} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="dateofbirth">Date Of Birth</label>
                        <input className='form-control' type="date" name="dateofbirth" id="dateofbirth" value={application?.dateofbirth} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="placeofbirth">Place Of Birth</label>
                        <input className='form-control' type="text" name="placeofbirth" id="placeofbirth" value={application?.placeofbirth} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="passportnation">Passport Nationality</label>
                        <input className='form-control' type="text" name="passportnation" id="passportnation" value={application?.passportnation} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="passportno">Passport Number</label>
                        <input className='form-control' type="text" name="passportno" id="passportno" value={application?.passportno} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="passportexpirydate">Passport Expiry Date</label>
                        <input className='form-control' type="date" name="passportexpirydate" id="passportexpirydate" value={application?.passportexpirydate} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="phoneno">Phone Number</label>
                        <input className='form-control' type="text" name="phoneno" id="phoneno" value={application?.phoneno} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="gender">Gender</label>
                        <input className='form-control' type="text" name="gender" id="gender" value={application?.gender} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="sponsername">Sponser Name</label>
                        <input className='form-control' type="text" name="sponsername" id="sponsername" value={application?.sponsername} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="purposeofvisa">Purpose Of Visa</label>
                        <input className='form-control' type="text" name="purposeofvisa" id="purposeofvisa" value={application?.purposeofvisa} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="profession">Profession</label>
                        <input className='form-control' type="text" name="profession" id="profession" value={application?.profession} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="email">Email</label>
                        <input className='form-control' type="email" name="email" id="email" value={application?.email} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="education">Education</label>
                        <input className='form-control' type="text" name="education" id="education" value={application?.education} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="contractyear">Contract Duration Year(In Year)</label>
                        <input className='form-control' type="text" name="contractyear" id="contractyear" value={application?.contractyear} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="probationmonth">No Of Probation Months</label>
                        <input className='form-control' type="text" name="probationmonth" id="probationmonth" value={application?.probationmonth} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="visanumber">Visa Number</label>
                        <input className='form-control' type="text" name="visanumber" id="visanumber" value={application?.visanumber} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="applicationumber">Application Number</label>
                        <input className='form-control' type="text" name="applicationumber" id="applicationumber" value={application?.applicationumber} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="visaowner">Description Of Visa Owner</label>
                        <input className='form-control' type="text" name="visaowner" id="visaowner" value={application?.visaowner} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="passporttype">Passport Type</label>
                        <input className='form-control' type="text" name="passporttype" id="passporttype" value={application?.passporttype} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="dateofissue">Date Of Issue</label>
                        <input className='form-control' type="date" name="dateofissue" id="dateofissue" value={application?.dateofissue} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="visavalidity">Visa Validity</label>
                        <input className='form-control' type="date" name="visavalidity" id="visavalidity" value={application?.visavalidity} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="basicsalary">Basic Salary</label>
                        <input className='form-control' type="text" name="basicsalary" id="basicsalary" value={application?.basicsalary} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="houseallowance">House Allowance</label>
                        <input className='form-control' type="text" name="houseallowance" id="houseallowance" value={application?.houseallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="transportallowance">Transport Allowance</label>
                        <input className='form-control' type="text" name="transportallowance" id="transportallowance" value={application?.transportallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="otherallowance">Other Allowance</label>
                        <input className='form-control' type="text" name="otherallowance" id="otherallowance" value={application?.otherallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="foodallowance">Food Allowance</label>
                        <input className='form-control' type="text" name="foodallowance" id="foodallowance" value={application?.foodallowance} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="totalsalary">Total Salary</label>
                        <input className='form-control' type="text" name="totalsalary" id="totalsalary" value={application?.totalsalary} onChange={(e) => changeHandler(e)} />
                    </div>

                </div>
                <div className="row p-2">
                    <div className='col-md-6'>
                        <label htmlFor="ticketduration">Ticket Duration</label>
                        <input className='form-control' type="text" name="ticketduration" id="ticketduration" value={application?.ticketduration} onChange={(e) => changeHandler(e)} />
                    </div>
                    <div className='col-md-6'>
                        <label htmlFor="visatype">Visa Type</label>
                        <input className='form-control' type="text" name="visatype" id="visatype" value={application?.visatype} onChange={(e) => changeHandler(e)} />
                    </div>
                </div>
                <div className="row p-2 ">
                    <div className='col-md-6'>
                        <button className='btn btn-primary' onClick={btnHandler} >Edit Application</button>
                    </div>
                    <div className='col-md-6 text-end'>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default EditApplication